/* Base styles for all screen sizes */
.courses {
  height: 100%;
}

.searchbar {
  display: flex;
  justify-content: space-between;
  padding: 2%;
  height: 3%;
  width: 94%;
  margin: 0 auto;
}

.no_matches {
  color: white;
  text-align: center;
  padding: 200px;
  font-size: 50px;
}

@media (max-width: 768px) {
  .searchbar {
    flex-direction: column;
    align-items: center;
    height: auto;
    padding: 10px;
  }

  .searchbar button {
    margin-top: 10px;
  }

  .no_matches {
    font-size: 30px;
    padding: 100px;
  }
}
