.header {
  display: flex;
  align-items: center;
  max-width: 100%;
  width: 94%;

  height: 15%;
  justify-content: space-between;
  border: solid 1px white;
  margin: 1% auto;
  padding: 1% 1%;

  gap: 15px;
}

.userInfo {
  color: white;
  font-size: 30px;
  float: right;
  position: absolute;
  right: 9%;
}

@media (max-width: 1360px) {
  .userInfo {
    margin-right: 30px;
  }
}

@media (max-width: 1000px) {
  .userInfo {
    margin-right: 80px;
  }
}
