/* CreateCourse.module.css */
.main_container {
  height: 100vh;
  width: 80%;
  margin: 0 auto;
  color: aliceblue;
}

.title {
  display: flex;
  justify-content: space-between;
  padding: 1% 4%;
  align-items: center;
  height: 100%;
}

.desc_input {
  display: flex;
  justify-content: center;
  width: 85%;
}

.add_authors {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: aliceblue;
  margin-left: 66px;
  margin-top: 50px;
}

.author_item {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 5%;
  align-items: center;
}

.authors_container {
  display: block;
  width: 50%;
  height: 100%;
  margin-right: 86px;
}

.author_name {
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 100px;
  align-self: flex-end;
  width: 100%;
}

.authors_list {
  border: 1px solid floralwhite;
  padding: 5% 5%;
  width: 96%;
  margin-top: 5%;
  margin-bottom: 3%;
}

.duration_container h1 {
  margin-bottom: 20px;
  margin-top: 20px;
}

.add_authors_container {
  width: 40%;
}
